  font-display: swap;
  src: url("/fonts/open-sans-light.woff2") format("woff2"), url("/fonts/open-sans-light.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

/*
- мобильная версия — 320px - 767px;
- планшетная версия — 768px - 1023px;
- десктопная версия — от 1024px и выше
*/
.contacts__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 558px;
  margin: 0 auto; }
  @media (max-width: 767px) {
    .contacts__wrapper {
      flex-direction: column; } }

.contacts-phone a,
.contacts-mail a {
  text-decoration: none;
  color: #000000;
  opacity: 0.5;
  font-size: 16px;
  line-height: 24px;
  padding: 20px 10px;
  display: flex;
  align-items: center; }

.contacts-mail a::before {
  content: '';
  width: 26px;
  height: 20px;
  margin-right: 15px;
  background-image: url("/img/icon-mail.svg");
  background-repeat: no-repeat;
  background-position: center; }

.contacts-phone a::before {
  content: '';
  width: 18px;
  height: 25px;
  margin-right: 15px;
  background-image: url("/img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: center; }

@media (min-width: 1024px) {
  .container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 40px; } }

.facts__wrapper h2 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600; }

.facts__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  counter-reset: list-counter;
  list-style: none;
  margin: 0;
  padding: 0; }
  @media (max-width: 1023px) {
    .facts__list {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px; } }
  @media (max-width: 767px) {
    .facts__list {
      display: block; } }

.facts-list__item {
  max-width: 263px;
  text-align: center;
  position: relative;
  counter-increment: list-counter;
  margin: 0 38px 0 0; }

.facts-list__item::before {
  position: absolute;
  width: 40px;
  height: 40px;
  left: 50%;
  transform: translateX(-50%);
  top: -45px;
  content: "0" counter(list-counter);
  font-weight: bold;
  font-size: 28px;
  line-height: 38px;
  text-align: center;
  color: #0ad9c6;
  border-radius: 4px; }

.form__wrapper {
  display: flex;
  flex-direction: column; }
  @media (max-width: 1023px) {
    .form__wrapper {
      align-items: center; } }

.form p {
  font-size: 17px;
  line-height: 28px;
  color: #ffffff;
  text-align: center; }
  @media (max-width: 1023px) {
    .form p {
      color: #000000;
      max-width: 321px; } }

.form__list {
  list-style: none;
  padding: 0;
  margin: 0; }

.form__button {
  display: inline-block;
  padding: 15px 48px;
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  color: #000000;
  text-decoration: none;
  border-radius: 6px;
  border: none;
  background-color: #0ad9c6; }
  @media (max-width: 1023px) {
    .form__button {
      padding: 15px 34px;
      max-width: 288px; } }
  .form__button:hover {
    background-color: rgba(10, 217, 198, 0.7); }
  .form__button:active {
    background-color: #00665e; }

.form-field__input-container input {
  display: flex;
  width: 100%;
  font-size: 15px;
  line-height: 24px;
  color: rgba(28, 28, 28, 0.5);
  box-sizing: border-box;
  border-radius: 6px;
  border: 1px solid #c9ccd4;
  background-color: #ffffff;
  padding: 14px 20px 10px 29px;
  margin: 0 0 20px 0; }
  @media (max-width: 1023px) {
    .form-field__input-container input {
      width: 288px; } }
  .form-field__input-container input:hover {
    border: 1px solid #999a9f;
    background-color: rgba(255, 255, 255, 0.7); }
  .form-field__input-container input:invalid:not(:placeholder-shown) {
    outline: 1px solid #990000; }
  .form-field__input-container input:focus {
    outline: 0;
    background-color: rgba(255, 255, 255, 0.7);
    border: 1px solid #999a9f;
    color: #ffffff; }

.gallery {
  background-color: #f6f7f9; }

.gallery__wrapper h2 {
